
import "./Skills.css"
import "./Skills.scss"

import {SkillsData, backendData} from "./PageComponents/SkillsData"

function Experience() {
    return (
        <div className="experience">
              <h1 className="frontend">Frontend</h1>
            <section className="exp-section">
               
                {SkillsData.map((item, index) => {
                        return(
                            <div key={index} className={item.cName}>
                                <div >
                                    {item.title}
                                    <div className="icons">
                                        {item.icon}
                                    </div>
                                </div>
                                {/* <div className="content">
                                    {item.text}
                                </div> */}
                                    
                                
                            </div>
                        )
                    })}


            </section>
            <h1 className="backend">Backend</h1>
            <section className="exp-section-two">
            {backendData.map((item, index) => {
                        return(
                            <div key={index} className={item.cName} >
                                <div >
                                    {item.title}
                                    <div className="icons">
                                        {item.icon}
                                    </div>
                                </div>
                                <div className="content">
                                    {item.text}
                                </div>
                                    
                                
                            </div>
                        )
                    })}
            </section>
        </div>
    )
}

export default Experience
